.f-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
}

.f-row {
    flex-wrap: wrap;
    display: flex;
    height: 100%;
    flex: 1;
}

.f-row.list {
    flex-direction: row;
}

.f-row.list.map {
    justify-content: center;
}

.f-row.list.socials {
    justify-content: end;
}

.f-col {
    display: flex;
    padding: 0 15px;
    align-items: center;
}

.f-col .link {
    font-size: 16px;
    font-weight: 550;
    padding: 5px 0;
    text-decoration: none;
    border-bottom: 2px solid transparent;
    transition: 0.5s ease-in-out;
}

.map .f-col .link:hover {
    color: var(--highlightColor);
    border-bottom: 2px solid var(--highlightColor);
    transition: 0.4s ease-in-out;
}

.f-row.list .social-icon .link:hover {
    color: var(--highlightColor);
    transition: 0.4s ease-in-out;
}

.f-col span {
    font-size: 12px;
}